 .slider {
    width: 100%;
    height: 728px;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
}

.slider-wrapper {
    justify-content: space-between;
}

.slider-title {
    padding-top: 139px;
    padding-left: 3px;
}

.left-slider {
    gap: 91px;
}

.right-slider {
    padding-top: 350px;
    justify-content: flex-end;
    align-items: flex-end;
}

.right-icon {
    width: 45px;
    height: 45px;
    background-size: cover;
    background-repeat: no-repeat;
}

.right-text {
    max-width: 281px;
}

.inner-right-slider-wrapper {
    gap: 20px;
}

.second-slider-button  {
    visibility: hidden;
    display: none;
}

.slider-image {
    position: absolute;
    width: 636px;
    height: 512px;
    left:53%;
    transform:translate(-53%, 0);
    top: 56px;
    background-repeat: no-repeat;
    background-size: cover;
}

.splide {
    display: none;
}

.left-slider {
	display: none !important;
}

.slider-image {
	display: none !important;
}

.right-slider {
	padding-top: 0;
}

.splide__slide {
    color: #000000;
}
